/* Container styling */
.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-family: 'Arial', sans-serif;
  }
  
  /* Page title */
  .accident-page h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
    color: #333;
    text-transform: uppercase;
  }
  
  /* Paragraph styling */
  .accident-page p {
    font-size: 1.2rem;
    margin-bottom: 20px;
    color: #666;
  }
  
  /* File input styling */
  .form-control {
    display: block;
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    color: #555;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: border-color 0.3s ease;
    margin-bottom: 15px;
  }
  
  .form-control:hover {
    border-color: #007bff;
  }
  
  /* Button styling */
  .btn-primary {
    padding: 10px 20px;
    font-size: 1.1rem;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    color: #fff;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
    box-shadow: 0 4px 12px rgba(0, 123, 255, 0.3);
  }
  
  .btn-primary:active {
    background-color: #004494;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .container {
      padding: 15px;
    }
  
    .accident-page h1 {
      font-size: 1.8rem;
    }
  
    .accident-page p {
      font-size: 1rem;
    }
  }
  
  /* Button animation */
  @keyframes buttonPress {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(4px);
    }
    100% {
      transform: translateY(0);
    }
  }
  
  .btn-primary:active {
    animation: buttonPress 0.2s ease-in-out;
  }
  
  /* Success message (optional, can be shown dynamically) */
  .success-message {
    font-size: 1.2rem;
    color: green;
    margin-top: 15px;
  }
  